
import { defineComponent } from "vue";
import { mapActions, mapState } from "vuex";
export default defineComponent({
  data() {
    return {};
  },
  computed: {
    ...mapState(["memberinfo"]),
  },
  created() {
    this.memberRecord();
    this.memberProfile();
  },
  methods: {
    ...mapActions(["MEMBERPROFILE", "MEMBERPROFILE"]),
    memberRecord() {
      this.MEMBERPROFILE();
    },
    memberProfile() {
      this.MEMBERPROFILE();
    },
  },
});
