
import Background from "@/components/common/Background.vue";
import UserPage_user from "@/components/organisms/user-Page/UserPage_user.vue";
import UserPage_record from "@/components/organisms/user-Page/UserPage_record.vue";
import UserPage_intro from "@/components/organisms/user-Page/UserPage_intro.vue";
import UserPage_recruit from "@/components/organisms/user-Page/UserPage_recruit.vue";
import UserPage_apply from "@/components/organisms/user-Page/UserPage_apply.vue";
import UserPage_set from "@/components/organisms/user-Page/UserPage_set.vue";
import { defineComponent } from "vue";
export default defineComponent({
  components: {
    Background,
    UserPage_user,
    UserPage_record,
    UserPage_intro,
    UserPage_recruit,
    UserPage_apply,
    UserPage_set,
  },
  data() {
    return {
      record: true,
      intro: false,
      recruit: false,
      apply: false,
      set: false,
    };
  },
  methods: {
    recordClick() {
      (this.record = true),
        (this.intro = false),
        (this.recruit = false),
        (this.apply = false),
        (this.set = false);
    },
    introClick() {
      (this.record = false),
        (this.intro = true),
        (this.recruit = false),
        (this.apply = false),
        (this.set = false);
    },
    recruitClick() {
      (this.record = false),
        (this.intro = false),
        (this.recruit = true),
        (this.apply = false),
        (this.set = false);
    },
    applyClick() {
      (this.record = false),
        (this.intro = false),
        (this.recruit = false),
        (this.apply = true),
        (this.set = false);
    },
    setClick() {
      (this.record = false),
        (this.intro = false),
        (this.recruit = false),
        (this.apply = false),
        (this.set = true);
    },
  },
});
