
import { defineComponent } from "vue";
import { mapActions, mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      // boards: [],
      // board: {
      //   board_no: 0,
      //   debate_topic: "",
      //   max_applicant: 0,
      //   current_applicant: 0,
      //   board_finished: "",
      // },
      // applicant_state: {
      //   login_id: "", // 추가 요청?
      //   board_no: 0, // 추가 요청?
      //   accept: 0,
      // },
    };
  },
  computed: {
    ...mapState([
      "boards",
      "debate_board",
      "applicant_apply",
      "applicant_apply_array",
      "memberinfo",
    ]),
  },
  created() {
    this.debateApply();
  },
  methods: {
    ...mapActions(["DEBATEAPPLY", "MEMBERINFO"]),
    async debateApply() {
      await this.DEBATEAPPLY();
    },
    moveToRoom(num: number) {
      console.log();
      this.$router.push("/room/" + num);
    },
    myApply(filter: any[]) {
      if (this.memberinfo.memberNo === filter[0]) {
        if (this.debate_board.debate_topic === filter[1]) return true;
        return false;
      }
      return false;
    },
    // consolelog(
    //   console.log(memberinfo.nickname);
    // )
  },
});
